import React, { useEffect, useState } from 'react';
import './css/Nav.css';
import { useNavigate } from 'react-router-dom';

export const Nav = React.memo(() => {

    /*
        [ useEffect ]
        : class componentDidMount, componentUpdate, componentWillUnMount   Hooks    .

        useEffect(function, [1, 2,...])
        -  mount function      rerendering  function .
        - function return  component unmount  (return    )

        ->    =>  component mount 
        ->   => component rerendering 
        ->   =>   rerendering function 
    */

    const [show, setShow] = useState(false);
    const [searchValue, setSearchValue] = useState("");

    // component mount  event  unmount  event  
    useEffect(() => {
        window.addEventListener("scroll", (e) => {
            if(window.scrollY > 50) {
                setShow(true);
            } else {
                setShow(false);
            }
        });

        return () => { // unmount    
            window.removeEventListener("scroll", () => {});
        }
    }, []); // mount, unmount  

    const navigate = useNavigate();

    const handleChange = (e) => {
        setSearchValue(e.target.value);
        navigate(`/search?q=${e.target.value}`);
    }
    

  return (
    <nav className={`nav ${show && "nav_black"}`}>
        <img 
            alt='Netflix logo'
            src='https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/170px-Netflix_2015_logo.svg.png'
            className='nav_logo'
            onClick = {() => window.location.reload()}
        />

        <input value={searchValue} onChange={handleChange} className='nav_input' type="text" placeholder=' .' />

        <img 
            alt="User logged"
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/170px-Netflix_2015_logo.svg.png"
            className='nav_avatar'
        />
    </nav>
  )
});
